@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.about {
  padding: 2em;
  width: 100vw;
  height: 94vh;
  background-color: white;
}

.btn-link{
  font-size: 24px;
  text-align: left;
}

.body {
  height: 306px;
}

#aboutAccordion {
  width: 70%;
}

#headshot {
  height: 80vh;
  width: 60vh;
}

#headshot img {
  width: 100%;
  height: 100%;
}

#downArrow img {
  width: 50px;
  margin: 10px;
}

#nameTitle {
  text-align: left;
  font-size: 66px;
  margin: 0.8em;
  font-family: 'Alata', sans-serif;
}

@media screen and (min-device-width: 800px) {
  #nameTitle {
    font-size: 50px;
  }
}

.skills {
  padding: 2.5em;
  min-height: 20vh;
  background-color: black;
  color: white;
}

.skills img {
  background-color: white;
  margin-top: 1em;
  height: 100px;
}

.contact {
  padding: 2em;
  min-height: 10vh;
  background-color: black;
  color: white;
}

.contact a {
  color: white;
}

.projectImages {
  margin-bottom: 20px;
}

li, p {
  text-align: left;
}

.projects {
  padding: 2em;
  min-height: 100vh;
  background-color: #246;
  width: 100%;
}

.projects h2 {
  margin-bottom: 1.6em;
  color: white;
}

.card-body:hover {
  cursor: pointer;
}

.techList {
  list-style-type: square;
}

#details {
  text-align: left;
}

#projectDisplay {
  margin: 2em;
  margin-left: 1em;
  margin-bottom: 20px;
  vertical-align: top;
}

#displayTitle {
  font-size: 34px;
  margin: 0.5em;
}

* {
  text-align: center;
  transition: 0.5s;
  font-family: 'Roboto', sans-serif;
}

#blackBar {
  background-color: black;
  height: 6vh;
}

